.my-team-page .title {
  display: flex;
  justify-content: space-between;
  padding: 0.9375rem;
}
.my-team-page .title .left {
  font-size: 1.125rem;
  font-weight: 400;
  color: #ffffff;
}
.my-team-page .title .right {
  font-size: 0.8125rem;
  font-weight: 400;
  color: #00D2FF;
}
.my-team-page .team-list {
  margin-top: 0.9375rem;
  padding-left: 0.9375rem;
  padding-right: 0.9375rem;
}
.my-team-page .team-list .team-item {
  background: linear-gradient(-30deg, #333333, #111111);
  border: 2px solid;
  border-image: linear-gradient(90deg, #ff2ef3, #4f00ff) 30 30 !important;
  height: 14.0625rem;
  margin-bottom: 0.9375rem;
  font-size: 0.75rem;
  color: #ffffff;
}
.my-team-page .team-list .team-item .top {
  padding-left: 1.5625rem;
  padding-right: 1.5625rem;
  padding-top: 0.9375rem;
  height: 10.9375rem;
  position: relative;
}
.my-team-page .team-list .team-item .top:after {
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  border-bottom: 1px solid #666666;
  color: #666666;
  transform-origin: 0 100%;
  transform: scaleY(0.5);
}
.my-team-page .team-list .team-item .top .muen-item {
  display: flex;
  justify-content: space-between;
  padding-bottom: 0.9375rem;
}
.my-team-page .team-list .team-item .top .right-btn {
  display: flex;
}
.my-team-page .team-list .team-item .top .right-btn .tip,
.my-team-page .team-list .team-item .top .right-btn .no-tip {
  min-width: 2.5rem;
  height: 1.09375rem;
  margin-left: 0.5625rem;
  padding-left: 0.5625rem;
  padding-right: 0.5625rem;
  border-radius: 0.09375rem;
  font-size: 0.625rem;
  line-height: 1.09375rem;
  text-align: center;
}
.my-team-page .team-list .team-item .top .right-btn .tip {
  background: linear-gradient(135deg, #20c988, #29c7a4);
}
.my-team-page .team-list .team-item .top .right-btn .no-tip {
  background: linear-gradient(90deg, #fd6e2f, #ff4e00);
}
.my-team-page .team-list .team-item .btn {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 1.875rem;
}
.my-team-page .add-team {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 4.0625rem;
  font-size: 1.125rem;
  color: #00D2FF;
}
.my-team-page .add-team .add-icon {
  font-size: 1.875rem;
}
.add-team-modal .title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 2.5rem;
  padding-left: 0.9375rem;
  padding-right: 0.9375rem;
  position: relative;
}
.add-team-modal .title:after {
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  border-bottom: 1px solid #666666;
  color: #666666;
  transform-origin: 0 100%;
  transform: scaleY(0.5);
}
.add-team-modal .title .right {
  font-size: 0.5625rem;
  color: #ffffff;
  font-weight: 400;
}
.add-team-modal .title .left {
  font-size: 0.875rem;
  color: #ffffff;
  font-weight: 400;
}
.add-team-modal .add-team-input {
  margin-top: 0.8125rem;
  background: rgba(255, 255, 255, 0);
}
.add-team-modal .add-team-input .am-list-item {
  background: rgba(255, 255, 255, 0);
  padding-left: 1.5625rem;
  padding-right: 1.5625rem;
}
.add-team-modal .add-team-input .am-list-item .am-list-line {
  position: relative;
  padding-left: 0.9375rem;
  padding-right: 0.9375rem;
}
.add-team-modal .add-team-input .am-list-item .am-list-line:after {
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  border-bottom: 1px solid #ffffff;
  color: #ffffff;
  transform-origin: 0 100%;
  transform: scaleY(0.5);
}
.add-team-modal .add-team-input .am-list-item .am-list-line input {
  font-size: 0.75rem;
  font-weight: 400;
  color: #666666;
}
.add-team-modal .add-team-input .am-list-item .am-list-line .am-input-extra {
  color: #00D2FF !important;
}
.add-team-modal .descript {
  padding-left: 0.9375rem;
  padding-top: 0.625rem;
}
.add-team-modal .descript .des-tip {
  display: flex;
  justify-content: flex-start;
  padding-left: 1.5625rem;
  font-size: 0.5625rem;
  color: #ffffff;
}
.add-team-modal .descript .des-tip .red {
  color: #FF3333 !important;
}
.add-team-modal .descript .des-tip .blue {
  color: #00D2FF;
}
.add-team-modal .message-error {
  display: flex;
  height: 0.625rem;
  justify-content: center;
  padding-top: 0.9375rem;
  padding-bottom: 2.5rem;
  font-size: 0.5625rem;
  color: #FF3333;
}
.add-team-modal .footerButton {
  margin-top: 2.5rem;
  padding-left: 1.5625rem;
  padding-right: 1.5625rem;
}
